import React from 'react';
import { Link as RouterLink } from 'react-router-dom';

// material-ui
import { makeStyles, useTheme } from '@material-ui/styles';
import { Box, Divider, Grid, Stack, Typography, useMediaQuery } from '@material-ui/core';

// project imports
import AuthWrapper2 from '../AuthWrapper2';
import AuthCardWrapper from '../AuthCardWrapper';
import Logo from 'ui-component/Logo';
import FirebaseRegister from '../firebase-forms/FirebaseRegister';
import BackgroundPattern2 from 'ui-component/cards/BackgroundPattern2';
import AuthSlider from 'ui-component/cards/AuthSlider';
import AuthFooter from 'ui-component/cards/AuthFooter';
import Footer from '../../landing/Footer'
// assets
import imgMain from 'assets/images/auth/img-a2-signup.svg';

const useStyles = makeStyles({
    authImage: {
        maxWidth: '100%',
        margin: '0 auto',
        display: 'block',
        width: '300px'
    }
});

// carousel items
const items = [
    {
        title: 'The Good Market',
        description: 'Powerful and easy to use shop'
    },
    {
        title: 'Accessible',
        description: 'Both vendor and seller Integration'
    },
    /* {
        title: 'Power of React with Material UI',
        description: 'Powerful and easy to use multipurpose theme'
    } */
];

// ===============================|| AUTH2 - REGISTER ||=============================== //

const Register = () => {
    const classes = useStyles();
    const theme = useTheme();
    const matchDownSM = useMediaQuery(theme.breakpoints.down('sm'));
    const matchDownMD = useMediaQuery(theme.breakpoints.down('md'));

    return (
        <AuthWrapper2>
            <Grid container style={{marginTop:-5}} justifyContent={matchDownSM ? 'center' : 'space-between'} alignItems="center">
                <Grid item md={6} lg={7} xs={12} sx={{ minHeight: '100vh' }}>
                    <Grid
                        sx={{ maxHeight: '100vh' }}
                        container
                        alignItems={matchDownSM ? 'center' : 'flex-start'}
                        justifyContent={matchDownSM ? 'center' : 'space-between'}
                    >
                        
                        <Grid
                            item
                            xs={12}
                            container
                            justifyContent="center"
                            alignItems="center"
                            sx={{ maxHeight: { xs: 'calc(100vh - 68px)', md: 'calc(100vh - 152px)' } }}
                        >
                            <Stack justifyContent="center" alignItems="center" spacing={5} m={2}>
                            
                                <AuthCardWrapper  border={matchDownMD}>
                                    <Grid container spacing={2} justifyContent="center"  >
                                        <Grid item xs={12} >
                                            <Stack alignItems="center" justifyContent="center" spacing={1}>
                                                <Grid item sx={{ display: { xs: 'none', md: 'block' }, m: 3 }}>
                                                    <RouterLink to="/">
                                                        <Logo />
                                                    </RouterLink>
                                                </Grid>
                                                <Typography style={{marginTop:-15}} variant="caption" fontSize="16px" textAlign={matchDownSM ? 'center' : ''}>
                                                    Enter your credentials to continue
                                                </Typography>
                                            </Stack>
                                        </Grid>
                                        <Grid item xs={12}>
                                            <FirebaseRegister />
                                        </Grid>
                                        <Grid item xs={12}>
                                            <Divider />
                                        </Grid>
                                        <Grid item xs={12} style={{marginTop:-14}}>
                                            <Grid item container direction="column" alignItems="center" xs={12} style={{marginBottom: -15}}>
                                                <Typography
                                                    component={RouterLink}
                                                    to="/login"
                                                    variant="subtitle1"
                                                    sx={{ textDecoration: 'none' }}
                                                >
                                                    Have an account?
                                                </Typography>
                                            </Grid>
                                        </Grid>
                                    </Grid>
                                </AuthCardWrapper>
                            </Stack>
                        </Grid>
                        {/* <Grid item xs={12} sx={{ m: 3 }}>
                            <AuthFooter />
                        </Grid> */}
                    </Grid>
                </Grid>
                <Grid item md={6} lg={5} sx={{ position: 'relative', alignSelf: 'stretch', display: { xs: 'none', md: 'block' } }}>
                    <BackgroundPattern2>
                        <Grid item container justifyContent="center">
                            <Grid item xs={12}>
                                <Grid item container justifyContent="center" sx={{ pb: 8 }}>
                                    <Grid item xs={10} lg={8} sx={{ '& .slick-list': { pb: 2 } }}>
                                        <AuthSlider items={items} />
                                    </Grid>
                                </Grid>
                            </Grid>
                            <Grid item xs={12}>
                                <img alt="Auth method" src={imgMain} className={classes.authImage} />
                            </Grid>
                        </Grid>
                    </BackgroundPattern2>
                </Grid>
                 
            </Grid>
           <Footer/>
        </AuthWrapper2>
    );
};

export default Register;
